import { AnyAction } from 'redux';


const ADD_TO_CART = 'ADD_TO_CART';

export const addToCart = (products: Product[]) => ({ type: ADD_TO_CART, products });

const reducer = (cart: Product[] = [], action: AnyAction) => {
  switch (action.type) {
    case ADD_TO_CART:
      return [...cart, action.products]

    default:
      return cart;
  }
};

export default reducer;
